import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
];

const router = new VueRouter({
  base: process.env.VUE_APP_AEC_BASE_URL,
  routes,
});

export default router;
